const AMap = window.AMap;


export function _getLocationAMap(callback){
  const mapObj = new AMap.Map('iCenter');
  mapObj.plugin('AMap.Geolocation', function () {
    let geolocation = new AMap.Geolocation({
        enableHighAccuracy: true,//是否使用高精度定位，默认:true
        timeout: 10000,          //超过10秒后停止定位，默认：无穷大
        maximumAge: 0,           //定位结果缓存0毫秒，默认：0
        convert: true,           //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
        showButton: true,        //显示定位按钮，默认：true
        buttonPosition: 'LB',    //定位按钮停靠位置，默认：'LB'，左下角
        buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
        showMarker: true,        //定位成功后在定位到的位置显示点标记，默认：true
        showCircle: true,        //定位成功后用圆圈表示定位精度范围，默认：true
        panToLocation: true,     //定位成功后将定位到的位置作为地图中心点，默认：true
        zoomToAccuracy:true      //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
    });
      mapObj.addControl(geolocation);
      geolocation.getCurrentPosition();
      AMap.event.addListener(geolocation, 'complete', (e)=>{
        const { lat,lng } = e.position
        const { district,province }=e.addressComponent
        if (typeof callback == "function") {
          callback({ latitude: lat , longitude: lng, district:district , province:province});
        }
      });//返回定位信息
      AMap.event.addListener(geolocation, 'error', ()=>{
        alert('定位出错')
      });      //返回定位出错信息
  });
}
// 判断是否是微信浏览器
export const isWeiXin = () => {
  let ua = window.navigator.userAgent.toLowerCase();
  if (ua.match(/MicroMessenger/i) === "micromessenger") {
    return true;
  } else {
    return false;
  }
};
//浏览器原生获取经纬度方法
export const latAndLon = (callback, error) => {
  if (navigator.geolocation) {
    navigator.geolocation.getCurrentPosition(
      (position) => {
        let latitude = position.coords.latitude;
        let longitude = position.coords.longitude;
        let data = {
          latitude: latitude,
          longitude: longitude,
        };
        if (typeof callback == "function") {
          callback(data);
        }
      },
      () => {
        if (typeof error == "function") {
          error();
        }
      }
    );
  } else {
    if (typeof error == "function") {
      error('浏览器不支持navigator.geolocation定位');
    }
  }
};

export const debounce = (fun, delay) => {
  //搜索时的方法
  return function (args) {
    let that = this;
    let _args = args;
    clearTimeout(fun.id);
    fun.id = setTimeout(function () {
      fun.call(that, _args);
    }, delay);
  };
};

