import React, { Component  } from 'react'
import {connect} from 'react-redux'//引入redux容器组件
import { CITY } from '@/redux/city_location/variable.js'//引入reducers变量
import { cityLocation } from '@/tool/location.js'
import position from '@/assets/imgs/position.png'
import pinyin from "js-pinyin" 
import './index.scss'
class Location extends Component {
    state = {
        list:[],//城市数据
        $alphabet:[],
        $tops:[],//每个元素距离顶部的高度
        $current:'A'
    }
    componentDidMount(){
        window.addEventListener('scroll', this._handleScroll)
        this.timerID=setTimeout(()=>{
            const { $alphabet } = this.state
            let $list=[]
            for(let i=0;i<$alphabet.length;i++){
                let $top=document.getElementById(`${$alphabet[i]}`)
                $list[i]=$top.offsetTop
            }
            this.setState({
                $tops: $list
            })
        },300)
        this._setCity()
    }
    componentWillUnmount (){
        // clearTimeout(this.timerID&&this.timerID)
        this.setState = (state,callback)=>{
            return;
          };
    }
    _handleScroll = () =>{
        const { $alphabet,$tops } = this.state
        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        let current = '';
        if(scrollTop<$tops[0]){
            current=$alphabet[0]
        }else{
            for (let i = 0, len = $tops.length; i < len - 1; i++) {
                if (scrollTop >= $tops[i] && scrollTop < $tops[i + 1]) {
                    current = $alphabet[i];
                }
            }
        }
        if (!current) current = $alphabet[$alphabet.length - 1];
        this.setState({$current:current})
    }
    _unique = (array) =>{
        let n = {}, r = [], val, type;
            for (let i = 0; i < array.length; i++) {
                val = array[i];
                type = typeof val;
                if (!n[val]) {
                    n[val] = [type];
                    r.push(array[i]);
                } else if (n[val].indexOf(type) < 0) {
                    n[val].push(type);
                    r.push(array[i]);
                }
            }
        return r;
    }
    _setSubItem = ( key, array ) =>{
        let arrayList=array
        arrayList=arrayList.filter( item => item['pinyin'].substr(0,1)===key)
        return arrayList
    }
    _getInitials = ( Initials ) =>{
        let keys=[]
        for(let i=0;i<Initials.length;i++){
            keys.push(Initials[i]['pinyin'].substr(0,1))
        }
        let r=this._unique(keys)
        return r
    }
    _getCitys = (res) =>{
        const cities = res
        cities.sort( (c1, c2) =>  c1.pinyin.localeCompare(c2.pinyin))
        let list = this._getInitials(cities)
        this.setState({
            $alphabet:list
        })
       return list
    }
    _setCity=async ()=>{
        // const {resultCode,cityCodeInfos}=await getCity()
        let r = cityLocation.map(function(item){
            item.pinyin = pinyin.getFullChars(item.city)
            return item
          })
        let keys=this._getCitys(r)
        let countList = []
        for (const key of keys) {
            countList.push({
              alpha: key,
              subItems: this._setSubItem(key,r)
            })
        }
        this.setState({list:countList})
    }
    _changeDOM = (initials,index) =>{
        const { $tops } = this.state
        let topHeight=$tops[index]
        window.scrollTo({ 
            top: topHeight, 
            behavior: "smooth" 
        })
        this.setState({
            $current:initials
        })
    }
    _changeCity = (country) =>{
        const { mylocation } =this.props
        mylocation['country']=country
        this.props._changeCity(mylocation)
        this.props.history.goBack()
    }
    render() {
        const {  mylocation  } = this.props
        const { list ,$current} = this.state
        return (
            <div className="location">
                <div className="location_city">
                    <img src={position} alt="" />
                    <span>定位城市：</span>
                    <span className="city">{ mylocation.city }</span>
                </div>
                <div className="location_item">
                    {
                        list.map((icon,index)=>{
                            return (
                                <div key={index}  className="item_initials" id={icon.alpha}>
                                    <div>{icon.alpha}</div>
                                    {
                                       icon.subItems.map((day,i)=>{
                                           return (
                                               <div key={i} className="initials_city" onClick={()=>this._changeCity(day.city)}>
                                                   <span>{day.city}</span>
                                               </div>
                                           )
                                       })
                                    }
                                </div>

                            )
                        })
                    }
                </div>
                <div className="initials_fiexed">
                    {
                        // $current
                        list.map( (item , index) =>{
                            return (
                                <div key={ index } className={($current===item.alpha)?'fiexed_boxcolor':'fiexed_box' }
                                 onClick={ ()=>this._changeDOM(item.alpha,index) }>
                                    <span>{item.alpha}</span>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}
const mapStateToProps=(state)=>({mylocation:state.locationCity})
const mapDisPatchToProps=(dispatch)=>({
    _changeCity:(number)=>{
        dispatch({type:CITY,data:number})
    },
})

export default connect(mapStateToProps,mapDisPatchToProps)(Location)
